html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 18px;
  line-height: 1;
  -webkit-tap-highlight-color: transparent;
}

body {
  background: #fff;
  font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto,  'Helvetica Neue', sans-serif;
  font-size: .8rem;
  overflow-x: hidden;
  text-rendering: optimizeLegibility;
  margin: 0;
}

::-moz-selection {
  background: #fff292;
}

::selection {
  background: #fff292;
}

a,
a:focus,
a:hover,
a:active {
  color: #000;
  outline: none;
  text-decoration: none;
}


.wrapper { 
  min-height: 100vh;
  padding: 0 1rem 4rem 1rem;
  margin: 0 15%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #ffffff;
}

header {
  -ms-flex-align: stretch;
      align-items: stretch;
  display: -ms-flexbox;
  display: flex;
  padding: 1.5rem 0;
  -ms-flex-direction: column;
      flex-direction: column;
}



/*
 *  Links 
 */
header .links ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding: 0;
}

header .links li {
  color: #000;
  font-size: .9rem;
  position: relative;
  display: inline-block;
}

header .links li:not(:first-child) {
  margin-left: 1.5rem;
}

header .links a {
  font-size: inherit;
  color: inherit;
  font-weight: 500;
  position: relative;
  -webkit-transition: color .2s ease-in-out;
  -o-transition: color .2s ease-in-out;
  transition: color .2s ease-in-out;
}

header .links .name {
  color: #999;
  font-weight: 400;
  font-family: 'SF Mono', 'Segoe UI Mono', 'Roboto Mono', Menlo, Courier, monospace;
}

header .links .name::before {
  content: ' (';
}
header .links .name::after {
  content: ') ';
}

/* Underline animation */
header .links a::after {
  position: absolute;
  content: '';
  display: block;
  left: 0;
  bottom: -.2rem;
  width: 100%;
  height: .05rem;
  background-color: #bbb;
  -webkit-transition: background-color .2s ease-in-out,
    -webkit-transform .2s ease;
  transition: background-color .2s ease-in-out,
    -webkit-transform .2s ease;
  -o-transition: transform .2s ease,
    background-color .2s ease-in-out;
  transition: transform .2s ease,
    background-color .2s ease-in-out;
  transition: transform .2s ease,
    background-color .2s ease-in-out,
    -webkit-transform .2s ease;
  -webkit-transform-origin: 0;
      -ms-transform-origin: 0;
          transform-origin: 0;
  -webkit-transform: scaleX(0);
      -ms-transform: scaleX(0);
          transform: scaleX(0);
}

/* hover styles */
header .links li a:hover::after,
header .links li a:focus::after {
  -webkit-transform: scaleX(1);
      -ms-transform: scaleX(1);
          transform: scaleX(1);
  background-color: #0f55eb;
}

header .links li a:hover,
header .links li a:focus {
  color: #0f55eb;
}



/*
 *  Install 
 */
header .install {
  padding-top: 1.5rem;
}

header .install p {
  background: #f6f6f7;
  padding: 1rem;
  font-family: 'SF Mono', 'Segoe UI Mono', 'Roboto Mono', Menlo, Courier, monospace;
}

header .install p::before {
  content: '$ ';
}

.divider {
  border-top: 1px solid #e7e9ed;
  height: 1px;
  margin: 14px 0 15px 0;
  text-align: center;
}

.divider::after {
  background: #fff;
  color: #aaa;
  content: 'OR';
  display: inline-block;
  font-size: .7rem;
  padding: 0 .4rem;
  -webkit-transform: translateY(-.45rem);
      -ms-transform: translateY(-.45rem);
          transform: translateY(-.45rem);
}



@media (max-width: 840px) {
  .hide-mobile {
    display: none;
  }

  .wrapper {
    margin: 0;
  }

  header {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
  }

  header .install {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: end;
        justify-content: flex-end;
    padding: 0;
  }

  header .install p {
    margin: 0;
  }
}

@media (max-width: 600px) {
  header {
    -ms-flex-direction: column;
        flex-direction: column;
  }
}




/*
 *  Tree table
 */
.demo-tree-table {
  background: #ffffff;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}

.demo-tree-table .cp_tree-table_row:not(:first-child) {
  -webkit-box-shadow: inset 0 1px 0 #eeeeee;
          box-shadow: inset 0 1px 0 #eeeeee;
}

.demo-tree-table .cell-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-weight: 300;
  font-size: 13px;
  height: 100%;
  width: 100%;

  font-family: 'SF Mono', 'Segoe UI Mono', 'Roboto Mono', Menlo, Courier, monospace;
}

.demo-tree-table .cell-wrapper > span {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.demo-tree-table .cell-wrapper > span.toggle-button-wrapper {
  -ms-flex-positive: 0;
      flex-grow: 0;
}

.demo-tree-table .cell-wrapper .toggle-button {
  background: #f0f3f5;
  color: #0f55eb;
  cursor: pointer;
  padding: 3px;
  border-radius: 3px;
}

.demo-tree-table .cell-wrapper .toggle-button:hover {
  background: #edf6fc;
}



